@import '../../gacolors.scss';

limel-dialog {
    &.preview-view {
        --dialog-width: 35rem;
        --dialog-height: 45rem;
    }

    &.aside {
        --dialog-width: 34rem;
    }

    .ga-version {
        position: absolute;
        top: 0;
        right: 50px;
    }

    .ga-body {
        overflow: auto;
        min-height: 100%;
        margin-top: 1rem;
    }

    .ga-top-bar {
        position: sticky;
        top: 0;
        height: 5rem;
        background-color: rgb(var(--contrast-100));
        z-index: 4;
        display: flex;
        align-items: center;
        z-index: 9;
    }

    @media (min-width: 1074px) {
        --dialog-width: 65rem;
        --dialog-height: 40rem;
    }

    @media (max-width: 1075px) {
        --dialog-width: 55rem;
        --dialog-height: 40rem;
    }

    @media (min-width: 1800px) {
        --dialog-width: 70rem;
        --dialog-height: 60rem;
    }

    --dialog-heading-icon-background-color: #f49132;
}

limel-button {
    --lime-primary-color: #f49132;
}

.logo-container {
    height: 2.2rem;
    width: 15rem;
    margin-bottom: 1rem;
    cursor: pointer;
    transition: width 0.5s;
    overflow: hidden;

    &.compact {
        width: 2.5rem;
    }

    &:hover {
        opacity: 0.8;
    }

    .logo {
        height: 2.2rem;
    }
}

.close {
    display: block;
    position: absolute;
    right: -1rem;
    top: 0rem;
    cursor: pointer;

    &:hover {
        color: #eee;
    }
}

@mixin button-type($color, $background-color, $highlight-color) {
    $ripple-color: lighten($highlight-color, 10%);

    color: $color;
    background: $background-color;

    &:hover {
        background: $highlight-color
            radial-gradient(circle, transparent 1%, $highlight-color 1%)
            center/15000%;
    }

    &:active {
        background-color: $ripple-color;
        background-size: 100%;
        transition: background 0s;
    }
}

.actionpad-container {
    width: 100%;
}

.getaccept-button {
    width: 100%;
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 0.425rem 0.875rem 0.425rem 0.875rem;

    border: none;
    border-radius: 2rem;
    cursor: pointer;

    transition: background 0.8s;
    background-position: center;
    font-size: 0.8rem;
    font-weight: bold;

    &:focus {
        outline: 0;
    }

    @include button-type($ga-light-black, #eee, #f5f5f5);

    img {
        height: 2rem;
        flex: 0;
    }

    .button-text {
        margin-left: 0.5rem;
        flex: 2;
        text-align: left;
    }

    .document-count {
        margin-left: auto;
        display: flex;
        align-items: center;

        limel-icon {
            margin-right: 0.3rem;
        }
    }
}

workflow-progress-bar {
    position: absolute;
    top: 1.5rem;
    left: 4rem;
    right: 4rem;
}
